body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  /*background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);*/
  /*background: linear-gradient(to right, rgb(22, 208, 167), rgb(194, 124, 202));*/
  background: -webkit-linear-gradient(left, rgb(72, 247, 238), rgb(223, 207, 166));
  background: linear-gradient(to right, rgb(72, 247, 238), rgb(223, 207, 166));
}

button {
	cursor: pointer;
}



.Tilt {
	background: -webkit-linear-gradient(left, rgb(72, 247, 238), rgb(223, 207, 166));
	background: linear-gradient(to right, rgb(72, 247, 238), rgb(223, 207, 166));
}
.form {
	width: 700px;
    background: -webkit-linear-gradient(right, rgb(72, 247, 238), rgb(223, 207, 166));
    background: linear-gradient(to left, rgb(72, 247, 238), rgb(223, 207, 166));
}
.App {
  text-align: center;
}

.center{
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left:0;
  z-index: -1;
}
